<template>
  <transition-group
    id="main"
    class="main-contents gridbox"
    name="fade"
    @before-enter="beforeEnter"
    appear
  >
    <div class="container" v-for="(content, index) in contents" :data-index="index" :key="index">
      <img
        class="content-image"
        :src="require('@/assets/thumbnails/' + content.src)"
        :alt="content.title"
        @click="click"
        :data-route="content.route"
      >
      <p class="content-title">{{ content.title }}</p>
    </div>
  </transition-group>
</template>

<script>
import Contents from "./contents.js";

export default {
  name: "mainContents",
  props: {},
  data() {
    return {
      contents: Contents.contents
    };
  },
  methods: {
    click(el) {
      this.$router.push({
        name: "detail",
        show: true,
        params: { title: el.target.dataset.route }
      });
    },
    beforeEnter(el) {
      el.style.transitionDelay = 100 * parseInt(el.dataset.index) + "ms";
    }
  }
};
</script>
<style src="./css/main-contents.css">