<template>
  <transition name="fade" appear v-show="show">
    <div class="wrapper" @click="click">
      <article>
        <div class="img-back">
          <button v-show="showImageSelector" class="prev button" @click="prevImage"></button>
          <img :src="src()">
          <button v-show="showImageSelector" class="next button" @click="nextImage"></button>
          <div class="image-select" v-show="showImageSelector">
            <div
              v-for="(o, idx) in opacity"
              class="circle"
              :style="{opacity: o}"
              :data-index="idx"
              :key="idx"
              @click="changeImage"
            ></div>
          </div>
        </div>
        <div class="description">
          <h1>{{ this.content.title }}</h1>
          <p>{{ this.content.description }}</p>
          <p class="site" v-if="this.content.site">
            Link :
            <a :href="this.content.site.url" target="_blank">{{ this.content.site.alt }}</a>
          </p>
        </div>
      </article>
    </div>
  </transition>
</template>

<script>
import Contents from "./contents.js"

export default {
  name: "detail",
  props: {
    show: Boolean
  },
  data() {
    var c = Contents.contents.filter(x => x.route === this.$route.params.title)[0]
    var o = Array(c.imageCount)
    o.fill(0.5)
    o[0] = 1
    var b = c.imageCount === 1 ? false : true
    return {
      content: c,
      imageIndex: 0,
      opacity: o,
      showImageSelector: b
    }
  },
  methods: {
    click(el) {
      if (
        !this.$el.classList.contains("fade-leave-to") &&
        el.target.classList.contains("wrapper")
      ) {
        this.$router.push({ name: "home" })
      }
    },
    src() {
      return require('@/assets/contents/' + this.content.route + '/' + this.imageIndex + '.png')
    },
    changeImage(el) {
      this.opacity[this.imageIndex] = 0.5
      var idx = el.target.dataset.index
      this.imageIndex = idx
      this.opacity[idx] = 1
    },
    nextImage(el) {
      this.opacity[this.imageIndex] = 0.5
      this.imageIndex = (this.imageIndex + 1) % this.content.imageCount
      this.opacity[this.imageIndex] = 1
    },
    prevImage(el) {
      this.opacity[this.imageIndex] = 0.5
      var prev = (this.imageIndex - 1) % this.content.imageCount
      this.imageIndex = prev < 0 ? this.content.imageCount - 1 : prev
      this.opacity[this.imageIndex] = 1
    }
  }
}
</script>

<style src="./css/overlay.css" scoped></style>
